.popup-wrapper {
    background: rgba(41, 41, 41, 0.6);
    top: 0px;
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
}

.popup {
    width: calc(100vw - 100px);
    height: calc(100vh - 100px);
    max-width: 500px;
    max-height: 600px;
    background: #FAFAFA;
    position: relative;
    border-radius: 9px;
}

.close-popup {
    position: absolute;
    right: 12px;
    top: 10px;
    cursor: pointer;
}

.close-popup:hover {
    opacity: 70%;
}

.popup-content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-top: 10px;
}

.ymaps-2-1-79-map-copyrights-promo,
.ymaps-2-1-79-copyright {
    display: none;
    width: 0;
}

.map {
    width: calc(100% - 20px);
    height: calc(100% - 60px);
}