.table {
    width: 100%;
}

th {
    cursor: pointer;
    font-weight: 400;
    background: #8a97a0;
    color: #FFF;
}

tr {
    background: #f4f7f8;
    border-bottom: 1px solid #FFF;
    margin-bottom: 5px;
}

tr:nth-child(even) {
    background: #e8eeef;
}

th,
td {
    text-align: center;
    padding: 5px;
    font-weight: 300;
}

tfoot tr {
    background: none;
}

tfoot td {
    padding: 10px 2px;
    font-size: 0.8em;
    font-style: italic;
    color: #8a97a0;
}

.toggle {
    padding-top: 1px;
    width: 50px;
    display: flex;
    height: 100%;
    justify-content: center;
}


.show-cities {
    align-items: center;
    justify-content: center;
    display: flex;
    align-self: center;
}

.header {
    display: flex;
    align-items: center;
    justify-content: center;
}

.arrow {
    font-size: 10px;
    padding-left: 10px;
}

.arrow:hover {
    opacity: 70%;
}