.autocomplete {
    position: absolute;
    z-index: 2;
}

.citiesInput {
    border: 1px solid #999;
    width: 200px;
    height: 20px;
    border-radius: 6px;
}

.no-suggestions {
    color: #999;
    padding: 0.1rem;
}

.suggestions {
    border: 1px solid #999;
    border-top-width: 0;
    list-style: none;
    margin-top: 0;
    max-height: 175px;
    overflow-y: auto;
    padding-left: 0;
    width: 204px;
    background-color: #FFF;
}

.suggestions li {
    padding: 0.5rem;
}

.suggestion-active,
.suggestions li:hover {
    background-color: rgb(176, 174, 174);
    cursor: pointer;
    font-weight: 700;
}

.suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #999;
}