.routeWrapper {
    display: flex;
    justify-content: center;
    min-width: 350px;
}


.route-locality {
    display: flex;
}

.routes {
    position: relative;
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.route {
    display: flex;
    align-items: center;
}

.plus,
.minus {
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 22px;
    width: 22px;
    cursor: pointer;
    margin: 0 8px;
}

.routeArrow {
    display: flex;
    padding: 0 5px;
    align-items: center;
}